import { FC, useContext, useEffect, useState } from "react";
import { AppContext } from "../../App";
import { getFavorites } from "../../helpers/api";
import { preloadImages } from "../../helpers/preloadImages";
import { IEvaluatingProfile } from "../../interfaces/IEvaluatingProfile";
import Candidate from "./Candidate";
import NoFavorites from "./NoFavorites";

const Favorites: FC = () => {
  const { isLoading, setIsLoading } = useContext(AppContext);

  const [favorites, setFavorites] = useState<IEvaluatingProfile[]>([]);
  const [isFavoritesLoading, setIsFavoritesLoading] = useState<boolean>(false);
  const [isNoFavorites, setIsNoFavorites] = useState<boolean>(false);

  const getNotEvaluatedUserIds = () =>
    favorites.filter((f) => !f.isEvaluated).map((f) => f.userId);

  useEffect(() => {
    const effect = async () => {
      const favorites = await getFavorites(getNotEvaluatedUserIds());

      if (favorites.length < 10) {
        setIsNoFavorites(true);
      }

      if (favorites.length) {
        setFavorites((prev) => [
          ...favorites.map((favorite) => ({
            ...favorite,
            isEvaluated: false,
          })),
          ...prev,
        ]);
        setIsFavoritesLoading(false);

        await Promise.all(
          favorites.map((favorite) =>
            preloadImages(favorite.userId, favorite.photoIds)
          )
        );
      }

      setIsLoading(false);
    };

    if (
      getNotEvaluatedUserIds().length < 10 &&
      !isNoFavorites &&
      !isFavoritesLoading
    ) {
      setIsFavoritesLoading(true);
      effect();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [favorites]);

  if (isLoading) return null;

  return (
    <>
      {isNoFavorites && !getNotEvaluatedUserIds().length && <NoFavorites />}

      {favorites.map((favorite, index) => (
        <Candidate
          profile={favorite}
          setProfiles={setFavorites}
          style={{
            display:
              favorite.isEvaluated ||
              index === getNotEvaluatedUserIds().length - 1
                ? "flex"
                : "none",
          }}
          key={favorite.userId}
        />
      ))}
    </>
  );
};

export default Favorites;
