import { FC, FocusEventHandler } from "react";
import { DesiredGender } from "../../enum/DesiredGender";
import { Gender } from "../../enum/Gender";
import { IUser } from "../../interfaces/IUser";
import Field from "../Field/Field";

interface IFieldGendersProps {
  user: IUser;
  setUser: Function;
  onBlur?: FocusEventHandler<HTMLSelectElement>;
}

const FieldGenders: FC<IFieldGendersProps> = ({
  user,
  setUser,
  onBlur,
}) => {
  return (
    <div style={{ display: "flex", width: "85%" }}>
      <Field title="Стать" style={{ marginRight: 10 }}>
        <select
          value={user.gender}
          onChange={(e) =>
            setUser((prev: IUser) => ({
              ...prev,
              gender: e.target.value,
            }))
          }
          onBlur={onBlur}
          className={user.gender === Gender.NO_SET ? "disabled" : ""}
        >
          <option value={Gender.NO_SET} disabled>
            Ваша стать
          </option>
          <option value={Gender.MALE}>Хлопець</option>
          <option value={Gender.FEMALE}>Дівчина</option>
        </select>
      </Field>

      <Field title="Цікавлять">
        <select
          value={user.desiredGender}
          onChange={(e) =>
            setUser((prev: IUser) => ({
              ...prev,
              desiredGender: e.target.value,
            }))
          }
          onBlur={onBlur}
          className={
            user.desiredGender === DesiredGender.NO_SET ? "disabled" : ""
          }
        >
          <option value={DesiredGender.NO_SET} disabled>
            Вас цікавлять
          </option>
          <option value={DesiredGender.FEMALE}>Дівчата</option>
          <option value={DesiredGender.MALE}>Хлопці</option>
          <option value={DesiredGender.BOTH}>Всі</option>
        </select>
      </Field>
    </div>
  );
};

export default FieldGenders;
