import { FC, FocusEventHandler } from "react";
import { CITIES } from "../../constants/cities";
import { City } from "../../enum/City";
import { IUser } from "../../interfaces/IUser";
import Field from "../Field/Field";

interface IFieldCityProps {
  user: IUser;
  setUser: Function;
  onBlur?: FocusEventHandler<HTMLSelectElement>;
}

const FieldCity: FC<IFieldCityProps> = ({ user, setUser, onBlur }) => {
  return (
    <Field title="Місто">
      <select
        value={user.cityId}
        onChange={(e) =>
          setUser((prev: IUser) => ({
            ...prev,
            cityId: e.target.value,
          }))
        }
        onBlur={onBlur}
        className={user.cityId === City.NO_SET ? "disabled" : ""}
      >
        <option value={City.NO_SET} disabled>
          Ваше місто
        </option>
        {/* Not through map for order changing */}
        <option value={City.LVIV}>{CITIES[City.LVIV]}</option>
        <option value={City.KYIV}>{CITIES[City.KYIV]}</option>
        <option value={City.VINNYTSIA}>{CITIES[City.VINNYTSIA]}</option>
        <option value={City.DNIPRO}>{CITIES[City.DNIPRO]}</option>
        <option value={City.DONETSK}>{CITIES[City.DONETSK]}</option>
        <option value={City.ZHYTOMYR}>{CITIES[City.ZHYTOMYR]}</option>
        <option value={City.ZAPORIZHIA}>{CITIES[City.ZAPORIZHIA]}</option>
        <option value={City.IVANO_FRANKIVSK}>
          {CITIES[City.IVANO_FRANKIVSK]}
        </option>
        <option value={City.KROPYVNYTSKYI}>{CITIES[City.KROPYVNYTSKYI]}</option>
        <option value={City.LUHANSK}>{CITIES[City.LUHANSK]}</option>
        <option value={City.LUTSK}>{CITIES[City.LUTSK]}</option>
        <option value={City.MYKOLAIV}>{CITIES[City.MYKOLAIV]}</option>
        <option value={City.ODESA}>{CITIES[City.ODESA]}</option>
        <option value={City.POLTAVA}>{CITIES[City.POLTAVA]}</option>
        <option value={City.RIVNE}>{CITIES[City.RIVNE]}</option>
        <option value={City.SIMFEROPOL}>{CITIES[City.SIMFEROPOL]}</option>
        <option value={City.SUMY}>{CITIES[City.SUMY]}</option>
        <option value={City.TERNOPIL}>{CITIES[City.TERNOPIL]}</option>
        <option value={City.UZHHOROD}>{CITIES[City.UZHHOROD]}</option>
        <option value={City.HARKIV}>{CITIES[City.HARKIV]}</option>
        <option value={City.KHERSON}>{CITIES[City.KHERSON]}</option>
        <option value={City.KHMELNYTSKYI}>{CITIES[City.KHMELNYTSKYI]}</option>
        <option value={City.CHERKASY}>{CITIES[City.CHERKASY]}</option>
        <option value={City.CHERNIVTSI}>{CITIES[City.CHERNIVTSI]}</option>
        <option value={City.CHERNIHIV}>{CITIES[City.CHERNIHIV]}</option>
      </select>
    </Field>
  );
};

export default FieldCity;
