import { FC, useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { AppContext } from "../../App";
import { getAllMessages } from "../../helpers/api";
import { getPhotoURL } from "../../helpers/getPhotoURL";
import { IAdminMessage } from "../../interfaces/IAdminMessage";

const AllMessages: FC = () => {
  const { chatId, secret } = useParams();
  const { setIsLoading } = useContext(AppContext);

  const [messages, setMessages] = useState<IAdminMessage[]>([]);

  useEffect(() => {
    const effect = async () => {
      const allMessages = await getAllMessages(chatId || "", secret || "");
      setMessages(allMessages);
      setIsLoading(false);
    };

    effect();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!messages.length) return null;

  return (
    <div className="chat">
      <div className="messages">
        {messages.map((message, i) => (
          <div
            className="message"
            style={{ display: "flex", alignItems: "center" }}
            key={i}
          >
            <div
              className="avatar"
              style={{
                width: 50,
                height: 50,
                backgroundSize: "cover",
                backgroundPosition: "center",
                borderRadius: 200,
                marginRight: 10,
                backgroundImage: `url(${getPhotoURL(
                  message.userId,
                  message.photoId
                )})`,
              }}
            />
            {message.payload
              .trim()
              .split("\n")
              .map((payload, index) => (
                <div key={index}>
                  <span>{payload}</span>
                  <br />
                </div>
              ))}
          </div>
        ))}
      </div>
    </div>
  );
};

export default AllMessages;
