import { FC, FocusEventHandler, useEffect, useState } from "react";
import { Month } from "../../enum/Month";
import { IUser } from "../../interfaces/IUser";
import Field from "../Field/Field";

interface IFieldBirthdayProps {
  user: IUser;
  setUser: Function;
  onBlur?: FocusEventHandler<HTMLSelectElement>;
}

const FieldBirthday: FC<IFieldBirthdayProps> = ({
  user,
  setUser,
  onBlur,
}) => {
  const [year, setYear] = useState("0000");
  const [month, setMonth] = useState("00");
  const [day, setDay] = useState("00");

  useEffect(() => {
    const [parsedYear, parsedMonth, parsedDay] = user.birthday.split("-");
    setYear(parsedYear);
    setMonth(parsedMonth);
    setDay(parsedDay);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const birthday = `${year}-${month}-${day}`;

    setUser((prev: IUser) => ({ ...prev, birthday }));
  }, [year, month, day, setUser]);

  const getYears = (): number[] => {
    const years = [];

    for (
      let i = new Date().getFullYear() - 18;
      i >= new Date().getFullYear() - 18 - 50;
      i--
    ) {
      years.push(i);
    }

    return years;
  };

  const getDays = (): string[] => {
    if (year === "0000" || month === "00") {
      return [];
    } else {
      const daysInMonth = new Date(Number(year), Number(month), 0).getDate();

      return Array.from({ length: daysInMonth }, (_, i) =>
        String(i + 1).padStart(2, "0")
      );
    }
  };

  return (
    <Field title="День народження">
      <div style={{ display: "flex" }}>
        <select
          value={year}
          onChange={(e) => setYear(e.target.value)}
          onBlur={onBlur}
          className={year === "0000" ? "disabled" : ""}
          style={{ width: "70%", marginRight: 10 }}
        >
          <option value="0000" disabled>
            Рік
          </option>
          {getYears().map((year) => (
            <option value={year} key={year}>
              {year}
            </option>
          ))}
        </select>
        <select
          value={month}
          onChange={(e) => setMonth(e.target.value)}
          onBlur={onBlur}
          className={month === Month.NO_SET ? "disabled" : ""}
          style={{ marginRight: 10 }}
        >
          <option value={Month.NO_SET} disabled>
            Місяць
          </option>
          <option value={Month.JANUARY}>Січень</option>
          <option value={Month.FEBRUARY}>Лютий</option>
          <option value={Month.MARCH}>Березень</option>
          <option value={Month.APRIL}>Квітень</option>
          <option value={Month.MAY}>Травень</option>
          <option value={Month.JUNE}>Червень</option>
          <option value={Month.JULY}>Липень</option>
          <option value={Month.AUGUST}>Серпень</option>
          <option value={Month.SEPTEMBER}>Вересень</option>
          <option value={Month.OCTOBER}>Жовтень</option>
          <option value={Month.NOVEMBER}>Листопад</option>
          <option value={Month.DECEMBER}>Грудень</option>
        </select>
        <select
          value={day}
          onChange={(e) => setDay(e.target.value)}
          onBlur={onBlur}
          disabled={year === "0000" || month === Month.NO_SET}
          className={day === "00" ? "disabled" : ""}
          style={{ width: "70%" }}
        >
          <option value="00" disabled>
            День
          </option>
          {getDays().map((day) => (
            <option value={day} key={day}>
              {day}
            </option>
          ))}
        </select>
      </div>
    </Field>
  );
};

export default FieldBirthday;
