import { FC, useContext, useEffect, useState } from "react";
import { AppContext } from "../../App";
import { City } from "../../enum/City";
import { DesiredGender } from "../../enum/DesiredGender";
import { Gender } from "../../enum/Gender";
import { Target } from "../../enum/Target";
import { getUser, updateUser } from "../../helpers/api";
import { IUser } from "../../interfaces/IUser";
import Field from "../Field/Field";
import FieldBirthday from "../Fields/FieldBirthday";
import FieldCity from "../Fields/FieldCity";
import FieldGenders from "../Fields/FieldGenders";
import FieldTarget from "../Fields/FieldTarget";
import UploadPhotos from "../UploadPhotos/UploadPhotos";
import "./Edit.scss";

const Edit: FC = () => {
  const { setUserProfileUpdater, isLoading } = useContext(AppContext);

  const [user, setUser] = useState<IUser>({
    photoIds: [],
    name: "",
    gender: Gender.NO_SET,
    desiredGender: DesiredGender.NO_SET,
    birthday: "0000-00-00",
    cityId: City.NO_SET,
    description: "",
    targetId: Target.NO_SET,
  });

  const updateUserHandler = async () => {
    if (user) {
      await updateUser(user);
      setUserProfileUpdater((prev) => prev + 1);
    }
  };

  useEffect(() => {
    const effect = async () => {
      const user = await getUser();
      setUser(user);
    };

    effect();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="edit" style={{ marginBottom: 60 }}>
      {!!user.photoIds.length && <UploadPhotos user={user} setUser={setUser} />}

      {!isLoading && (
        <>
          <Field title="Ім'я">
            <input
              type="text"
              value={user.name}
              onChange={(e) =>
                setUser((prev) => prev && { ...prev, name: e.target.value })
              }
              onBlur={updateUserHandler}
              placeholder="Ваше ім'я"
            />
          </Field>

          <FieldGenders
            user={user}
            setUser={setUser}
            onBlur={updateUserHandler}
          />

          <FieldBirthday
            user={user}
            setUser={setUser}
            onBlur={updateUserHandler}
          />

          <FieldCity user={user} setUser={setUser} onBlur={updateUserHandler} />

          <FieldTarget
            user={user}
            setUser={setUser}
            onBlur={updateUserHandler}
          />

          <Field title="Опис">
            <textarea
              value={user.description}
              onChange={(e) =>
                setUser(
                  (prev) => prev && { ...prev, description: e.target.value }
                )
              }
              onBlur={updateUserHandler}
              rows={3}
              placeholder="Ваш опис"
            />
          </Field>
        </>
      )}
    </div>
  );
};

export default Edit;
