import { createContext, Dispatch, FC, SetStateAction, useState } from "react";
import { Route, Routes, useLocation } from "react-router-dom";
import Menu from "./components/Menu/Menu";
import Edit from "./components/Edit/Edit";
import Registration from "./components/Registration/Registration";
import Chat from "./components/Chat/Chat";
import ProfileFromChat from "./components/Profiles/ProfileFromChat";
import UserProfile from "./components/Profiles/UserProfile";
import Candidates from "./components/Profiles/Candidates";
// import Loading from "./components/Loading/Loading";
import Favorites from "./components/Profiles/Favorites";
// import Chats from "./components/Chats/Chats";
import "./App.scss";
import AllUsers from "./components/Profiles/AllUsers";
import AllChats from "./components/Chats/AllChats";
import AllMessages from "./components/Chat/AllMessages";

interface IAppContext {
  isLoading: boolean;
  setIsLoading: Dispatch<SetStateAction<boolean>>;
  userProfileUpdater: number;
  setUserProfileUpdater: Dispatch<SetStateAction<number>>;
}

export const AppContext = createContext<IAppContext>({
  isLoading: true,
  setIsLoading: () => {},
  userProfileUpdater: 0,
  setUserProfileUpdater: () => {},
});

const App: FC = () => {
  const { pathname } = useLocation();

  const [isLoading, setIsLoading] = useState(true);
  const [userProfileUpdater, setUserProfileUpdater] = useState(0);

  return (
    <AppContext.Provider
      value={{
        isLoading,
        setIsLoading,
        userProfileUpdater,
        setUserProfileUpdater,
      }}
    >
      <div className="app">
        {/* {isLoading && <Loading />} */}

        <Routes>
          <Route path="/registration" element={<Registration />} />
          <Route path="/find" element={<Candidates />} />
          <Route path="/favorites" element={<Favorites />} />
          <Route path="/chats" element={<Chat />} />
          <Route
            path="/chats/:chatId/:photoId/:userId/:name"
            element={<Chat />}
          />
          <Route path="/chats/profile/:userId" element={<ProfileFromChat />} />
          <Route path="/profile" element={<UserProfile />} />
          <Route path="/edit" element={<Edit />} />
          <Route path="/all-users/:secret" element={<AllUsers />} />
          <Route path="/all-chats/:secret" element={<AllChats />} />
          <Route path="/all-chats/:chatId/:secret" element={<AllMessages />} />
        </Routes>

        {!["/registration", "/find"].includes(pathname) &&
          !pathname.startsWith("/chat") &&
          !pathname.startsWith("/all-") && <Menu />}
      </div>
    </AppContext.Provider>
  );
};

export default App;
