import { useEffect, FC, useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../../App";
import { City } from "../../enum/City";
import { DesiredGender } from "../../enum/DesiredGender";
import { Gender } from "../../enum/Gender";
import { Month } from "../../enum/Month";
import { Step } from "../../enum/Step";
import { Target } from "../../enum/Target";
import { signUp } from "../../helpers/api";
import { IUser } from "../../interfaces/IUser";
import Field from "../Field/Field";
import FieldBirthday from "../Fields/FieldBirthday";
import FieldCity from "../Fields/FieldCity";
import FieldGenders from "../Fields/FieldGenders";
import UploadPhotos from "../UploadPhotos/UploadPhotos";
import "./Registration.scss";

const Registration: FC = () => {
  const navigate = useNavigate();
  const { setIsLoading } = useContext(AppContext);

  const [step, setStep] = useState(Step.NAME_AND_GENDER);
  const [user, setUser] = useState<IUser>({
    photoIds: [],
    name: window.Telegram?.WebApp?.initDataUnsafe.user?.first_name || "",
    gender: Gender.NO_SET,
    desiredGender: DesiredGender.NO_SET,
    birthday: "0000-00-00",
    cityId: City.NO_SET,
    description: "",
    targetId: Target.NO_SET,
  });

  const toSignUp = async () => {
    const isSuccess = await signUp(user);

    if (isSuccess) navigate("/find");
  };

  const isCurrentStepValid = (): boolean => {
    switch (step) {
      case Step.NAME_AND_GENDER:
        return (
          user.name.length > 2 &&
          user.gender !== Gender.NO_SET &&
          user.desiredGender !== DesiredGender.NO_SET
        );
      case Step.BIRTHDAY_AND_CITY:
        const [parsedYear, parsedMonth, parsedDay] = user.birthday.split("-");

        return (
          parsedYear !== "0000" &&
          parsedMonth !== Month.NO_SET &&
          parsedDay !== "00" &&
          user.cityId !== City.NO_SET
        );
      case Step.UPLOAD_PHOTOS:
        return user.photoIds.length > 0;
      default:
        return false;
    }
  };

  useEffect(() => {
    setIsLoading(false);

    if (step > Step.UPLOAD_PHOTOS) {
      toSignUp();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [step]);

  return (
    <>
      <div className="registration">
        {step === Step.NAME_AND_GENDER && (
          <>
            <Field title="Ім'я">
              <input
                type="text"
                value={user.name}
                onChange={(e) =>
                  setUser((prev) => ({ ...prev, name: e.target.value }))
                }
                placeholder="Ваше ім'я"
              />
            </Field>
            <FieldGenders user={user} setUser={setUser} />
          </>
        )}

        {step === Step.BIRTHDAY_AND_CITY && (
          <>
            <FieldBirthday user={user} setUser={setUser} />
            <FieldCity user={user} setUser={setUser} />
          </>
        )}

        {step === Step.UPLOAD_PHOTOS && (
          <UploadPhotos user={user} setUser={setUser} />
        )}

        {isCurrentStepValid() && (
          <span
            className="material-symbols-rounded next"
            onClick={() => setStep((prev) => prev + 1)}
          >
            east
          </span>
        )}
      </div>
    </>
  );
};

export default Registration;
