import Compressor from "compressorjs";

const compressPhoto = (
  photo: File | Blob,
  quality: number
): Promise<File | Blob> => {
  return new Promise((resolve, reject) => {
    new Compressor(photo, {
      quality: quality / 100,
      success: resolve,
      error: reject,
    });
  });
};

export const compressPhotoToFixedSize = async (
  photo: File | Blob,
  quality: number = 100,
  limitSize: number = 500 * 1024
): Promise<File | Blob | null> => {
  const compressedPhoto = await compressPhoto(photo, quality);

  if (compressedPhoto.size > limitSize) {
    quality -= 10;

    if (quality < 0) {
      window.Telegram.WebApp.showAlert(
        "Не вдалось обробити фото, спробуйте зменшити його розмір"
      );
      return null;
    }

    return compressPhotoToFixedSize(photo, quality);
  }

  return compressedPhoto;
};
