import { FC, ReactNode, CSSProperties } from "react";
import "./Field.scss";

interface IFieldProps {
  title: string;
  children: ReactNode;
  style?: CSSProperties;
}

const Field: FC<IFieldProps> = ({ title, children, style }) => {
  return (
    <div className="field" style={style}>
      <p>{title}</p>
      {children}
    </div>
  );
};

export default Field;
