import { getPhotoURL } from "./getPhotoURL";

const preloadImage = (userId: string, photoId: string) =>
  new Promise((resolve, reject) => {
    const img = new Image();
    const src = getPhotoURL(userId, photoId);

    img.onload = function () {
      resolve(img);
    };
    img.onerror = img.onabort = function () {
      reject(src);
    };
    img.src = src;
  });

export const preloadImages = async (userId: string, photoIds: string[]) => {
  const imagesPromiseList: Promise<any>[] = [];
  for (const photoId of photoIds) {
    imagesPromiseList.push(preloadImage(userId, photoId));
  }

  await Promise.all(imagesPromiseList);
};

export const preloadImagesOfDifferentUsers = async (
  images: { userId: string; photoId: string }[]
) => {
  const imagesPromiseList: Promise<any>[] = [];
  for (const image of images) {
    imagesPromiseList.push(preloadImage(image.userId, image.photoId));
  }

  await Promise.all(imagesPromiseList);
};
