import { FC, useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../../App";
import { getCandidates } from "../../helpers/api";
import { UserHaveNoProfile, UserIsBanned } from "../../helpers/errors";
import { preloadImages } from "../../helpers/preloadImages";
import { IEvaluatingProfile } from "../../interfaces/IEvaluatingProfile";
import Menu from "../Menu/Menu";
import Banned from "./Banned";
import Candidate from "./Candidate";
import NoCandidates from "./NoCandidates";

const Candidates: FC = () => {
  const navigate = useNavigate();
  const { isLoading, setIsLoading } = useContext(AppContext);

  const [candidates, setCandidates] = useState<IEvaluatingProfile[] | null>(
    null
  );
  const [isCandidatesLoading, setIsCandidatesLoading] =
    useState<boolean>(false);
  const [isNoCandidates, setIsNoCandidates] = useState<boolean>(false);
  const [isBanned, setIsBanned] = useState<boolean>(false);

  const getNotEvaluatedUserIds = () =>
    (candidates ?? []).filter((c) => !c.isEvaluated).map((c) => c.userId);

  useEffect(() => {
    const savedLoadedCandidates = JSON.parse(
      localStorage.getItem("candidates") || "[]"
    ) as IEvaluatingProfile[];

    setCandidates(savedLoadedCandidates);

    if (savedLoadedCandidates.length >= 10) {
      setIsLoading(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const effect = async () => {
      try {
        const newCandidates = await getCandidates(
          (candidates ?? []).map((c) => c.userId)
        );

        if (newCandidates.length < 10) {
          setIsNoCandidates(true);
        }

        if (newCandidates.length) {
          setCandidates((prev) => [
            ...newCandidates.map((candidate) => ({
              ...candidate,
              isEvaluated: false,
            })),
            ...(prev ?? []),
          ]);
          setIsCandidatesLoading(false);

          await Promise.all(
            newCandidates.map((candidate) =>
              preloadImages(candidate.userId, candidate.photoIds)
            )
          );
        }
      } catch (error) {
        if (error instanceof UserHaveNoProfile) {
          navigate("/registration");
        } else if (error instanceof UserIsBanned) {
          setIsBanned(true);
        }
      }

      setIsLoading(false);
    };

    if (
      getNotEvaluatedUserIds().length < 10 &&
      !isNoCandidates &&
      !isCandidatesLoading &&
      candidates !== null
    ) {
      setIsCandidatesLoading(true);
      effect();
    }

    if (candidates !== null) {
      localStorage.setItem("candidates", JSON.stringify(candidates));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [candidates]);

  if (isLoading) return null;
  if (isBanned) return <Banned />;

  return (
    <>
      {isNoCandidates && !getNotEvaluatedUserIds().length && <NoCandidates />}

      {candidates !== null &&
        candidates?.map((candidate, index) => (
          <Candidate
            profile={candidate}
            setProfiles={setCandidates}
            style={{
              display:
                candidate.isEvaluated ||
                index === getNotEvaluatedUserIds().length - 1
                  ? "flex"
                  : "none",
            }}
            key={candidate.userId}
          />
        ))}

      <Menu />
    </>
  );
};

export default Candidates;
