import axios, { AxiosError, AxiosRequestConfig } from "axios";
import { IUser } from "../interfaces/IUser";
import { IMessage } from "../interfaces/IMessage";
import { IChatPreview } from "../interfaces/IChatPreview";
import { IProfile } from "./../interfaces/IProfile";
import {
  DailyQuotaForLikesIsReached,
  UserHaveNoProfile,
  UserIsBanned,
} from "./errors";
import { ISignUpInput } from "./interfaces/ISignUpInput";
import { IAdminChatPreview } from "../interfaces/IAdminChatPreview";
import { IAdminMessage } from "../interfaces/IAdminMessage";

const BASE_URL =
  "https://ttxqav6ea7.execute-api.eu-central-1.amazonaws.com/prod";

const onRequest = (config: AxiosRequestConfig): AxiosRequestConfig => {
  config.headers = {
    "Init-Data": window.Telegram.WebApp.initData,
  };

  return config;
};

axios.interceptors.request.use(onRequest);

export const getCandidates = async (
  notEvaluatedUserIds: string[]
): Promise<IProfile[]> => {
  try {
    const res = await axios.post(`${BASE_URL}/candidates`, {
      notEvaluatedUserIds,
    });

    return res.data;
  } catch (error) {
    if (error instanceof AxiosError && error.response?.status === 401) {
      throw new UserHaveNoProfile();
    }
    if (error instanceof AxiosError && error.response?.status === 403) {
      throw new UserIsBanned();
    }

    return [];
  }
};

export const signUp = async (data: ISignUpInput): Promise<boolean> => {
  const res = await axios.post(`${BASE_URL}/sign-up`, data);

  return res.status === 201;
};

export const uploadPhoto = async (base64Photo: string) => {
  const res = await axios.post(`${BASE_URL}/photo`, { photo: base64Photo });

  return res.data; // photoId
};

export const deletePhoto = (photoId: string): void => {
  axios.delete(`${BASE_URL}/photo/${photoId}`);
};

export const getFavorites = async (
  notEvaluatedUserIds: string[]
): Promise<IProfile[]> => {
  const res = await axios.post(`${BASE_URL}/favorites`, {
    notEvaluatedUserIds,
  });

  return res.data;
};

export const getProfile = async (userId: string): Promise<IProfile> => {
  const res = await axios.get(`${BASE_URL}/profile/${userId}`);

  return res.data;
};

export const likeProfile = async (toUserId: string): Promise<void> => {
  try {
    await axios.post(`${BASE_URL}/profile/like`, { toUserId });
  } catch (error) {
    if (error instanceof AxiosError && error.response?.status === 403) {
      throw new DailyQuotaForLikesIsReached();
    }
  }
};

export const dislikeProfile = (toUserId: string): void => {
  axios.post(`${BASE_URL}/profile/dislike`, { toUserId });
};

export const getUser = async (): Promise<IUser> => {
  const res = await axios.get(`${BASE_URL}/user`);

  return res.data;
};

export const updateUser = async (data: IUser): Promise<void> => {
  await axios.put(`${BASE_URL}/user`, data);
};

export const getChats = async (): Promise<IChatPreview[]> => {
  const res = await axios.get(`${BASE_URL}/chats`);

  return res.data;
};

export const getMessages = async (chatId: string): Promise<IMessage[]> => {
  const res = await axios.get(`${BASE_URL}/chats/${chatId}`);

  return res.data;
};

export const sendMessage = async (
  chatId: string,
  payload: string
): Promise<void> => {
  await axios.post(`${BASE_URL}/message`, { chatId, payload });
};

export const getAllUsers = async (secret: string): Promise<IProfile[]> => {
  const res = await axios.get(`${BASE_URL}/all-users/${secret}`);

  return res.data;
};

export const getAllChats = async (
  secret: string
): Promise<IAdminChatPreview[]> => {
  const res = await axios.get(`${BASE_URL}/all-chats/${secret}`);

  return res.data;
};

export const getAllMessages = async (
  chatId: string,
  secret: string
): Promise<IAdminMessage[]> => {
  const res = await axios.get(`${BASE_URL}/all-messages/${chatId}/${secret}`);

  return res.data;
};
