import { FC, useContext, useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { AppContext } from "../../App";
import { getAllChats } from "../../helpers/api";
import { getPhotoURL } from "../../helpers/getPhotoURL";
import { IAdminChatPreview } from "../../interfaces/IAdminChatPreview";

const AllChats: FC = () => {
  const { secret } = useParams();
  const { setIsLoading } = useContext(AppContext);

  const [chats, setChats] = useState<IAdminChatPreview[]>([]);

  useEffect(() => {
    const effect = async () => {
      const allChats = await getAllChats(secret || "");
      setChats(allChats);
      setIsLoading(false);
    };

    effect();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!chats.length) return null;

  return (
    <>
      <p style={{ textAlign: "center", margin: "20px 0" }}>{chats.length}</p>

      <div className="chats" style={{ padding: 0 }}>
        {chats.map((chat) => (
          <Link
            to={`/all-chats/${chat.chatId}/${secret}`}
            className="chat-preview"
            onClick={() => {
              setIsLoading(true);
            }}
            key={`chat-${chat.chatId}`}
            style={{ justifyContent: "space-between" }}
          >
            <div
              className="avatar"
              style={{
                backgroundImage: `url(${getPhotoURL(
                  chat.collaborators[0].userId,
                  chat.collaborators[0].photoId
                )})`,
              }}
            />
            <div
              className="info"
              style={{ flexFlow: "row", alignItems: "center" }}
            >
              <div className="name">
                {chat.collaborators[0].name} {chat.collaborators[0].userId}
              </div>
              <p
                style={{ margin: "0 10px", color: "rgba(255, 255, 255, 0.5)" }}
              >
                and
              </p>
              <div className="name">
                {chat.collaborators[1].name} {chat.collaborators[1].userId}
              </div>
            </div>
            <div
              className="avatar"
              style={{
                backgroundImage: `url(${getPhotoURL(
                  chat.collaborators[1].userId,
                  chat.collaborators[1].photoId
                )})`,
              }}
            />
          </Link>
        ))}
      </div>
    </>
  );
};

export default AllChats;
