import { CSSProperties, FC, useState } from "react";
import { dislikeProfile, likeProfile } from "../../helpers/api";
import { DailyQuotaForLikesIsReached } from "../../helpers/errors";
import { IEvaluatingProfile } from "../../interfaces/IEvaluatingProfile";
import { IProfile } from "../../interfaces/IProfile";
import Card from "../Card/Card";

interface ICandidateProps {
  profile: IProfile;
  setProfiles: Function;
  style?: CSSProperties;
}

const Candidate: FC<ICandidateProps> = ({ profile, setProfiles, style }) => {
  const [isLiked, setIsLiked] = useState(false);
  const [isDisliked, setIsDisliked] = useState(false);

  const toLike = async () => {
    // prevent double clicking
    if (isLiked || isDisliked) return;

    setIsLiked(true);

    try {
      markProfileEvaluation(true);
      await likeProfile(profile.userId);
      removeProfile();
    } catch (error) {
      if (error instanceof DailyQuotaForLikesIsReached) {
        markProfileEvaluation(false);
        setIsLiked(false);
        window.Telegram.WebApp.showAlert("Використано денний ліміт лайків");
      }
    }
  };

  const toDislike = async () => {
    // prevent double clicking
    if (isLiked || isDisliked) return;

    setIsDisliked(true);
    dislikeProfile(profile.userId);
    markProfileEvaluation(true);
    removeProfile();
  };

  const markProfileEvaluation = (isEvaluated: boolean) => {
    setProfiles((prev: IEvaluatingProfile[]) => {
      prev.map((_person) => {
        if (_person.userId === profile.userId) {
          _person.isEvaluated = isEvaluated;
        }

        return _person;
      });

      return [...prev];
    });
  };

  const removeProfile = () => {
    setTimeout(() => {
      setProfiles((prev: IEvaluatingProfile[]) =>
        prev.filter((_person) => _person.userId !== profile.userId)
      );
    }, 1000);
  };

  return (
    <Card
      profile={profile}
      buttons={
        <div className="buttons">
          <span className="material-symbols-rounded" onClick={toDislike}>
            close
          </span>
          <span
            className="material-symbols-rounded"
            onClick={toLike}
            style={{ color: "#d93025" }}
          >
            favorite
          </span>
        </div>
      }
      isZoomInDownAnimation={true}
      isLiked={isLiked}
      isDisliked={isDisliked}
      style={style}
    />
  );
};

export default Candidate;
